import { Component, OnInit } from '@angular/core';
import { CollectDataService } from '../../services/collect-data.service'
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-new-user',
  templateUrl: './new-user.component.html',
  styleUrls: ['./new-user.component.css']
})
export class NewUserComponent implements OnInit {
  user: any;
  constructor(private router:Router,private route:ActivatedRoute,private collectData:CollectDataService) { }

  ngOnInit() {
    this.collectData.createNewUser().subscribe(user=>{
      this.user = user;
      // console.log(this.user._id);
      this.router.navigate(['/'+this.user._id])
    });
  }
  
}
